<template>
  <div>
    <!-- 头部标题 -->
    <div class="header">用户管理</div>
    <!-- 内容区域 -->
    <div class="container">
      <!-- 搜搜区域 -->
      <div class="searchBox">
        <input
          class="searchInput"
          v-model="search.keyword"
          type="text"
          placeholder="请输入用户ID或用户名"
        />

        <button type="button" class="searchBtn" @click="searchUser">
          搜索
        </button>
      </div>
      <!-- 展示列表区 -->
      <div class="listBox">
        <div class="list">
          <template>
            <el-table :data="tableData" border stripe style="width: 100%">
              <el-table-column
                prop="uid"
                label="用户ID"
                width="120"
                align="center"
              >
              </el-table-column>
              <el-table-column
                prop="uname"
                label="用户名"
                width="150"
                align="center"
              >
              </el-table-column>
              <el-table-column
                prop="tel"
                label="用户电话"
                width="150"
                align="center"
              >
              </el-table-column>
              <el-table-column label="用户类型" width="120" align="center">
                <template v-slot="{ row }">
                  <el-button
                    type="primary"
                    size="mini"
                    v-if="row.flag == 'c'"
                    disabled
                    >普通用户</el-button
                  >
                  <el-button type="success" size="mini" disabled v-else
                    >店铺用户</el-button
                  >
                </template>
              </el-table-column>
              <el-table-column label="用户状态" width="120" align="center">
                <template v-slot="{ row }">
                  <el-button
                    type="primary"
                    size="mini"
                    v-if="row.status == 0"
                    disabled
                    >正常</el-button
                  >
                  <el-button type="info" size="mini" disabled v-else
                    >冻结</el-button
                  >
                </template>
              </el-table-column>
              <el-table-column label="操作" align="center">
                <template v-slot="{ row }">
                  <el-button size="mini" type="primary" @click="userDeatil(row)"
                    >查看</el-button
                  >
                  <el-button
                    size="mini"
                    type="danger"
                    v-if="row.status == 0"
                    @click="changeStatus(row.uid, 1, row.flag)"
                    >冻结</el-button
                  >
                  <el-button
                    size="mini"
                    type="info"
                    v-else
                    @click="changeStatus(row.uid, 0, row.flag)"
                    >解冻</el-button
                  >
                </template>
              </el-table-column>
            </el-table>
          </template>
        </div>
        <!-- 分页器 -->
        <div class="pager">
          <el-pagination
            @current-change="handleCurrentChangeFn"
            :current-page.sync="search.pagenum"
            :page-size.sync="search.pagesize"
            layout="total, prev, pager, next, jumper"
            :total="total"
          >
          </el-pagination>
        </div>
      </div>
    </div>
    <!-- 表单dialog -->
    <el-dialog title="用户详情" :visible.sync="dialogFormVisible">
      <el-form :model="form">
        <el-form-item label="用户头像" :label-width="formLabelWidth">
          <img
            v-if="!form.uavatar"
            src="../../assets/images/defaultLOGO.jpg"
            style="width: 100px; height: 100px"
          />
          <img v-else :src="form.uavatar" style="width: 100px; height: 100px" />
        </el-form-item>
        <!-- 菜品ID -->
        <el-form-item label="用户ID" :label-width="formLabelWidth">
          <el-input disabled v-model="form.uid" autocomplete="off"></el-input>
        </el-form-item>
        <!-- 发布时间 -->
        <el-form-item label="用户名" :label-width="formLabelWidth">
          <el-input disabled v-model="form.uname" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="用户密码" :label-width="formLabelWidth">
          <el-input disabled v-model="form.pwd" autocomplete="off"></el-input>
        </el-form-item>
        <!-- 公告标题 -->
        <el-form-item label="用户电话" :label-width="formLabelWidth">
          <el-input disabled v-model="form.tel" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="用户类型" :label-width="formLabelWidth">
          <el-button type="primary" v-if="form.flag == 'c'" disabled
            >普通用户</el-button
          >
          <el-button type="success" size="mini" disabled v-else
            >店铺用户</el-button
          >
        </el-form-item>
        <el-form-item label="用户状态" :label-width="formLabelWidth">
          <el-button type="primary" v-if="form.status == 0" disabled
            >正常</el-button
          >
          <el-button type="success" disabled v-else>冻结</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
import {
  getUserListAPI,
  searchUserAPI,
  updateUserStatusAPI,
  updateShopAPI,
} from "@/api/index";
export default {
  name: "userCMD",
  // ==========================================以下是data==========================================
  data() {
    return {
      search: {
        pagenum: 1,
        pagesize: 6,
        keyword: "",
      },
      // 总共的轮播图数
      total: 0,

      // 用于控制dialogform
      dialogFormVisible: false,
      // 用于控制表单label的宽
      formLabelWidth: "120px",
      // 用于收集表单的数据
      form: {
        uid: "",
        uname: "",
        tel: "",
        flag: "",
        status: "",
        uavatar: "",
        pwd: "",
      },
      tableData: [
        {
          uid: "20200001",
          uname: "第一号用户",
          tel: "19184562524",
          flag: "c",
          status: 0,
          uavatar: "",
          pwd: "123456789",
        },
      ],
    };
  },
  // ==========================================以下是data==========================================
  methods: {
    // 初始化用户列表
    async initUserListFn() {
      const { data: res } = await getUserListAPI(this.search);
      if (res.code != 0) return this.$message.error("获取用户列表失败！");
      this.tableData = res.data;
      this.total = res.count;
    },
    // 页码发生改变时调用
    handleCurrentChangeFn(newPage) {
      // 为页码值赋值
      this.search.pagenum = newPage;
      if (this.search.keyword != "") {
        this.searchUserAction();
      }
      // 重新发起请求
      this.initUserListFn();
    },
    // 搜索用户
    searchUser() {
      if (this.search.keyword == "") {
        this.$message("请输入关键词");
        return this.initUserListFn();
      }
      this.search.pagenum = 1;
      this.searchUserFn();
    },
    async searchUserFn() {
      const { data: res } = await searchUserAPI(this.search);
      if (res.code != 0) return this.$message.error("搜索失败！");
      this.tableData = res.data;
      this.total = res.count;
    },
    userDeatil(user) {
      this.dialogFormVisible = true;
      this.form = { ...user };
      console.log(user);
      console.log(this.form);
    },
    // 修改店铺状态
    async changeShopStatus(form) {
      const { data: res } = await updateShopAPI(form);
      if (res.code != 0) return this, this.$message.error("店铺信息修改失败！");
    },
    // 修改用户状态
    async changeStatus(uid, status, flag) {
      const { data: res } = await updateUserStatusAPI({ uid, status });
      if (res.code != 0) return this.$message.error("用户状态切换失败！");
      if (flag == "b") {
        let form = res.info;
        form = { ...form, status: status };
        this.changeShopStatus(form);
      }
      this.search.pagenum = 1;
      this.initUserListFn();
    },
  },
  // ==========================================以下是mounted==========================================
  mounted() {
    this.initUserListFn();
  },
};
</script>

<style scoped>
.header {
  height: 60px;
  width: 80%;
  margin: 0 auto;
  text-align: center;
  line-height: 60px;
  font-size: 40px;
  font-family: "幼圆";
  font-weight: 600;
  color: rgb(84, 87, 87);
  background-color: rgb(197, 199, 205);
  border: rgb(183, 184, 187) solid 10px;
  border-radius: 10px;
}
.container {
  width: 90%;
  height: 485px;
  background-color: rgb(186, 193, 193);
  margin: 10px auto;
  border-radius: 10px;
  overflow: hidden;
}
.searchBox {
  width: 40%;
  height: 40px;
  margin: 10px auto;
  vertical-align: middle;
  overflow: hidden;
}
.searchInput {
  width: 80%;
  height: 30px;
  font-size: 15px;
  border-radius: 6px;
  border: rgb(242, 240, 210) solid 1px;
}
.searchBtn {
  width: 60px;
  height: 30px;
  font-size: 15px;
  color: rgb(67, 70, 72);
  background-color: rgb(169, 193, 225);
  border: rgb(127, 171, 228) solid 2px;
  border-radius: 6px;
  cursor: pointer;
  margin-left: 10px;
}
.searchBtn:active {
  background-color: rgb(248, 248, 248);
  border: rgb(232, 233, 235) solid 2px;
}
.listBox {
  width: 80%;
  height: 400px;
  background-color: rgb(244, 244, 245);
  border-radius: 5px;
  margin: auto;
  overflow: hidden;
}
.list {
  width: 98%;
  height: 360px;
  margin: 8px auto;
  overflow: hidden;
}
.pager {
  width: 65%;
  height: 30px;
  margin: auto;
  margin-top: -8px;
}
</style>
